<template>
  <ModalDialogTemplateVue style="width:400px">
    <template v-slot:header>
      알림
    </template>

    <template v-slot:body>
        <p>추가하고 싶은 카테고리 이름을 입력하세요</p>
        <div class="form-group">
          <input type="text" name="cname" placeholder="카테고리 이름" v-model="categoryName.cname">
        </div>
        <button id="btn_for_submit" @click="categoryAdd" style="display:none">submit 버튼</button>
    </template>

    <template v-slot:footer>
      <!--밑의 추가 버튼 : 폼 submit과 emit(confirm) 이 같이 실행됨-->
      <button class="btn btn-info btn-sm" 
      onclick="$('#btn_for_confirm').trigger('click'); $('#btn_for_submit').trigger('click');">추가</button>
      <button class="btn btn-info btn-sm"  @click="$emit('cancel')">닫기</button>
      <button id="btn_for_confirm" style="display:none" @click="$emit('confirm')">폼 submit을 위해 임시로 만든 버튼</button>
    </template>

  </ModalDialogTemplateVue>
</template>

<script setup>
import ModalDialogTemplateVue from '@/components/popup/ModalDialogTemplate.vue';
import { reactive, defineProps } from "vue";
import { useRouter } from 'vue-router';
import apiBoard from "@/apis/board";
import { useStore } from "vuex";

const router = useRouter();
const store = useStore();
const userId = store.state.userId;

const categoryName = reactive({
  cname : "",
  mid : userId
})

const pageInfo = defineProps({
  pageNo: String,
  cname: String
});

async function categoryAdd(){
  const result = await apiBoard.createCategory(categoryName);
  router.push("/listboard");
}
</script>

